.reviews {
    padding: 16px;

    @media screen and (min-width: 744px) {
        padding: 20px;
    }
}

.reviewsList {
    display: flex;
    flex-direction: column;
    gap: 15px;

    @media screen and (min-width: 744px) {
        gap: 25px;
    }
}

.noReviewsText {
    font-size: 16px;
    font-weight: 400;

    @media screen and (min-width: 1280px) {
        font-size: 18px;
    }
}